<template>
  <div class="shopbag">
    <van-nav-bar title="购物车" left-text="返回" left-arrow @click-left="back">
      <template #right>
        <div @click="toggleEditStatus" v-if="shopbagData.length > 0">{{!isEdit ? '编辑' : '完成'}}</div>
      </template>
    </van-nav-bar>
    <div v-if="shopbagData.length > 0">
      <div class="shopbag-bg">
          <p class="p1">
              <img src="../assets/img/gsdgae.png">
              咫尺的醇香、真情的回味
          </p>
          <div class="imgs">
              <img src="../assets/img/faf.png">
          </div>
          <p class="p2">
              <img src="../assets/img/20181130142508471.png" alt="">
          </p>
      </div>

      <div class="shopbag-box">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="买的不够多啊！多买点"
          offset="100"
          @load="loadShopbagData"
        >
          <van-swipe-cell
            class="shopbag-cell"
            :disabled="isEdit"
            v-for="(item, index) in shopbagData"
            :key="item.sid"
          >
            <div class="shopbag-item">
              <div class="check">
                <van-checkbox v-model="item.isChecked" icon-size="20px" @change="simpleSelect"></van-checkbox>
              </div>
              <div class="pro-info">
                <div class="pro-img">
                  <img class="auto-img" :src="item.small_img" alt />
                </div>
                <div class="pro-text">
                  <div class="text-box">
                    <div class="pro-names">
                      <div class="pro-name">{{item.name}}</div>
                      <div class="pro-rule">{{item.rule}}</div>
                    </div>
                    <div class="pro-enname">{{item.enname}}</div>
                  </div>
                  <div class="count-box">
                    <div class="pro-price">&yen;{{item.price}}</div>
                    <div class="pro-count">
                      <van-stepper
                        v-model="item.count"
                        theme="round"
                        button-size="22"
                        disable-input
                        @change="updateShopbagCount(item.sid, item.count)"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <template #right>
              <van-button
                class="shopbag-remove-btn"
                color="#0C34BA"
                square
                text="删除"
                @click="removeShopbag([item.sid], index)"
              />
            </template>
          </van-swipe-cell>
        </van-list>
      </div>

      <van-submit-bar v-show="!isEdit" :price="total" @submit="comit"  button-text="提交订单" button-color="#f6c73d">
        <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>

      <van-submit-bar
        v-show="isEdit"
        button-text="删除选择"
        button-color="#0C34BA"
        @submit="removeSelect"
      >
        <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>
    </div>
    <div v-else>
      <van-empty image="https://img.yzcdn.cn/vant/custom-empty-image.png" description="购物车没有数据，赶紧去逛一逛！">
        <div class="guang" @click="goHome">逛一逛</div>
      </van-empty>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "Shopbag",

  data() {
    return {
      //是否编辑商品
      isEdit: false,

      //全选或者取消全选
      checked: false,

      shopbagAllData: [],

      //购物袋数据
      shopbagData: [],

      //开始截取位置
      start: 0,

      count: 7,

      loading: true,

      finished: false,

      //总金额
      total: 0,
    };
  },

  created() {
    this.getShopbagData();
  },

  computed: {
    ...mapState(["shopbagCount"]),
  },

  methods: {
    ...mapMutations(["changeShopbagCount"]),

    //返回上一级
    back() {
      this.$router.go(-1);
    },


    goHome() {
      this.$router.push({ name: "Honm" });
    },

    //切换编辑状态
    toggleEditStatus() {
      this.isEdit = !this.isEdit;
    },

    //获取购物袋数据
    getShopbagData() {
      let tokenString = localStorage.getItem("tokenString");
      // 

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === 5000) {
            res.data.result.forEach((v) => {
              v.isChecked = false;
            });

            this.shopbagAllData = res.data.result;

            this.shopbagData = this.shopbagAllData.slice(
              this.start,
              this.start + this.count
            );

            this.start += this.count;

            if (this.shopbagAllData.length < this.count) {
              this.finished = true;
              return;
            }

            //下次触底进行懒加载
            this.loading = false;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //加载数据
    loadShopbagData() {
      setTimeout(() => {
        
        // 

        let data = this.shopbagAllData.slice(
          this.start,
          this.start + this.count
        );

        // 
        this.shopbagData.push(...data);
        // 
        this.start += this.count;

        //取消全选
        if (data.length > 0) {
          this.checked = false;
        }

        //如果本次截取不足this.count条数据, 下次不可能有数据
        if (data.length < this.count) {
          
          this.finished = true;
          return;
        }

        //下次进行触底懒加载
        this.loading = false;
      }, 1000);
    },

    //修改购物袋数量
    updateShopbagCount(sid, count) {
      let tokenString = localStorage.getItem("tokenString");
      // 

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid,
          count,
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === 6000) {
            this.sum();
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //提交订单
    comit(){
      let sids = [];
      this.shopbagData.forEach((v) => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });

      if (sids.length === 0) {
        this.$toast({
          message: "选择下单的商品",
          forbidClick: true,
          duration: 3000,
        });

        return;
      }
      this.$router.push({name:'Pay',query:{ sids: sids.join('-')}})
      // 
    },

    //全选
    allSelect() {
      //将所有单选状态修改位全选状态
      this.shopbagData.forEach((v) => {
        v.isChecked = this.checked;
      });

      this.sum();
    },

    //单选
    simpleSelect() {
      this.sum();

      //只要有一个单选没有勾选，则全选不能勾选
      for (let i = 0; i < this.shopbagData.length; i++) {
        if (!this.shopbagData[i].isChecked) {
          this.checked = false;
          return;
        }
      }

      this.checked = true;
    },

    //删除购物袋数据
    removeShopbag(sids, index) {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/deleteShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids),
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code == 7000) {
            //如果是单个
            if (index !== undefined) {
              this.shopbagData.splice(index, 1);

              this.changeShopbagCount(this.shopbagCount - 1);

              this.simpleSelect();
            } else {
              //执行多个删除
              for (let i = 0; i < this.shopbagData.length; i++) {
                if (this.shopbagData[i].isChecked) {
                  this.shopbagData.splice(i, 1);
                  i--;
                }
              }

              this.changeShopbagCount(this.shopbagCount - sids.length);
              this.sum();
            }

            this.$nextTick(() => {
              let cells = document.querySelectorAll(".shopbag-cell");

              // 

              //获取最后一个节点距离可视区域的尺寸
              let cellSzie = cells[cells.length - 1].getBoundingClientRect();

              //获取最后一个节点距离可视区域页面顶部的距离
              let distance = cellSzie.top;
              // 

              //获取最后一个节点的高度
              let cellHeight = cellSzie.height;

              //获取屏幕高度
              let innerHeight = window.innerHeight;
              // 

              if (distance + cellHeight - innerHeight <= 50) {

                this.loadShopbagData();
              }
            });
          }

          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //删除选择的购物袋商品
    removeSelect() {
      //获取勾选的sid
      let sids = [];
      this.shopbagData.forEach((v) => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });

      if (sids.length === 0) {
        this.$toast({
          message: "未选择删除商品",
          forbidClick: true,
          duration: 3000,
        });

        return;
      }

      

      this.removeShopbag(sids);
    },

    //计算勾选的商品总金额
    sum() {
      let total = 0;
      this.shopbagData.forEach((v) => {
        if (v.isChecked) {
          total += v.price * v.count;
        }
      });

      this.total = total * 100;
    },

  },
};
</script>
<style lang="less" scoped>
.shopbag {
  padding-bottom: 50px;
  /deep/ .van-nav-bar .van-icon, /deep/ .van-nav-bar__text{
    color:#f6c73d;
  }
  .shopbag-bg{
      width: 100%;
      position: relative;
      color: white;
      text-align: center;
      font-size: 18px;
      .p1{
          display: block;
          position: absolute;
          top: 0px;
          left: 0px;
      }
      .p2{
          display: block;
          width: 95%;
          position: absolute;
          bottom: -15px;
          left:5px;
      }
      img{
          width: 100%;
      }
  }
     .pro-img{
        width: 100px;
        height: 100px;
    }

    .auto-img{
        width: 100px;
        height: 100px;
        }

  .shopbag-box{
    padding: 10px 10px 0;
  }

  .shopbag-item{
    display: flex;
    padding: 10px;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  .check{
    width: 20px;
    height: 20px;
    margin-top: 30px;
  }

  .pro-info{
    display: flex;
    margin-left: 10px;
    width: calc(100% - 32px);
  }

  .pro-img{
    width: 80px;
  }

  .pro-text{
    width: calc(100% - 90px);
    height: 80px;
    margin-left: 10px;
  }

  /deep/ .van-checkbox__icon--checked .van-icon{
    background-color: #f6c73d;
    border-color: #f6c73d;
  }

  .text-box{
    height: 50px;
  }

  .count-box{
    height: 30px;
  }

  .pro-names{
    display: flex;
    margin-left: 25px;
    margin-bottom: 5px;
  }

  .pro-name{
    font-size: 14px;
    margin-right: 15px;
  }

  .pro-rule{
    color: #999;
    font-size: 12px;
    height: 19px;
    line-height: 21px;
  }

  .pro-enname{
    color: #999;
    font-size: 12px;
    margin-left: 25px;
  }

  .count-box{
    display: flex;
  }

  .pro-price{
    line-height: 30px;
    color: #f6c73d;
    font-weight: bold;
    font-size: 14px;
    margin-right: auto;
    margin-left: 25px;
  }
  .pro-count{
    padding-top: 4px;
  }

  /deep/ .van-stepper--round .van-stepper__plus{
    background-color: #f6c73d;
  }

  /deep/ .van-stepper--round .van-stepper__minus{
    color: #f6c73d;
    border-color: #f6c73d;
  }

  /deep/ .shopbag-remove-btn{
    height: 100%;
  }

  .shopbag-cell{
    margin-bottom: 10px;
  }

  /deep/ .van-submit-bar{
    bottom: 50px;
  }

  /deep/ .van-submit-bar__price{
    color: #f6c73d;
  }

  /deep/ .van-submit-bar__button{
    margin-left: auto;
  }

  .guang{
    color: #fff;
    font-size: 12px;
    width: 70px;
    height: 28px;
    background-color: #f6c73d;
    text-align: center;
    line-height: 28px;
    border-radius: 14px;
  }
}
</style>